.castContainer{
    padding-left: 20px;
    padding-right: 20px;
}
.castWrapper{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding-left: 40px;
    padding-right: 40px;
    padding-top: 40px;
    padding-bottom: 40px;
    max-width: 800px;
    gap: 40px;
    margin: 0 auto;
      -webkit-box-shadow: 0px 10px 38px 7px rgba(0, 0, 0, 0.5);
    -moz-box-shadow: 0px 10px 38px 7px rgba(0, 0, 0, 0.5);
    box-shadow: 0px 10px 38px 7px rgba(0, 0, 0, 0.5);
    border-radius: 8px;
    margin-bottom: 40px;
    max-width: 800px;
}
.castImage{
    border-radius: 6px;
}
.castInfo{
    display: flex;
    flex-direction: column;
    gap: 8px;
}